<template>
	<section>
		<div class="inf-zst-t">
			<div class="inf-info tieshui">当前位置：<a href="">首页 &gt;</a><a href="javascript:;">实货贴水走势图</a></div>
	      	<span class="inf-zt-gb"><a href="javascript:;" @click="backFn">×</a></span>
	      	<div class="cg-sc-zst">
	      		<div id="main" style="width:600px;height:400px;" v-show="num == 0"></div>
	      		<div class="officalData" v-show="num ==1">
		      		<p>暂无有关数据</p>
		      	</div>
		      	<div class="officalData" v-show="num ==2">
		      		<p>请至少选择一种油种</p>
		      	</div>
	      	</div>
	        <ul class="cg-sc-xz clearfix">
	           	<li v-for="item in oilTypes"><input type="checkbox" class="c-check" :value="item" v-model="arr">{{item}}</li>
	        </ul>
	        <div class="block">
			  <span class="demonstration">请选择年月</span>
			  <el-date-picker
			    v-model="yearMonth"
			    align="right"
			    type="month"
			    value-format="yyyy-MM"
			    placeholder="选择年">
			  </el-date-picker>
			</div>
	    </div>
	    
	</section>
</template>

<script>
	import echarts from 'echarts'
	import { xianHuo,huoZhong } from '../../api/info'; 
	export default{
		data(){
			return{
				oilTypes:[],
				num:0,
				arr:[],
				data:5,
				options:{
					 tooltip: {
				        trigger: 'axis',
				        axisPointer: {
				            type: 'cross',
				            crossStyle: {
				                color: '#999'
				            }
				        }
				    },
				    xAxis: [
				        {
				            type: 'category',
				            data: []
				        }
				    ],
				    yAxis: [
				        {
				            type: 'value',
//				            name: '水量',
//				            min: 0,
//				            max: 250,
//				            interval: 50,
//				            axisLabel: {
//				                formatter: '{value} ml'
//				            }
				        },
				        {
				            type: 'value',
//				            name: '温度',
////				            min: 0,
//				            max: 25,
//				            interval: 5,
//				            axisLabel: {
//				                formatter: '{value} °C'
//				            }
				        }
				    ],
				    series: [
				        {
				            name:'',
				            type:'line',
				            data:[]
				        },
				        {
				            name:'',
				            type:'line',
				            data:[]
				        },
				        {
				            name:'',
				            type:'line',
//				            yAxisIndex: 1,
				            data:[]
				        }
				    ]
				},
				yearMonth:'2018-02',
				num:0
			}
		},
		methods:{
			huoZhongFn(){
				var param={
					pricingDate:this.yearMonth
				}
				
				huoZhong(param).then(res =>{
					this.oilTypes = res.datas
					this.arr[0]= res.datas[0]
					
					var para={
						'crudeNameArray':this.arr,
						'pricingDate':this.yearMonth
					}
					var myChart = echarts.init(document.getElementById('main'));
					var option12 = $.extend(true, option12, this.options);
	   				xianHuo(para).then(res=>{
	   					option12.series[0].name = res.datas[0].crudeNameE
	   					for(var i = 0;i<res.datas[0].dataList.length;i++){
	   						option12.xAxis[0].data.push(res.datas[0].dataList[i].pricingDate)
	   						option12.series[0].data.push(res.datas[0].dataList[i].pricing)
	   					}
	   					 myChart.setOption(option12);
	   				})
	   				
				})
			},
			backFn(){
				this.$router.push({path:'/shts'})
			}
		},
		watch:{
			arr:function(val){
				if(val.length==0){
					this.num = 2
					return false
				}
				if(val.length > 3){
					this.arr = val.slice(0,3)
//					alert('做多只能选择三条')
					this.$message({
			          message: '最多只能选择三种',
			          type: 'warning'
			        });
				}else{
					var myChart = echarts.init(document.getElementById('main'));
					var option12 = $.extend(true, option12, this.options);
					var para={
						'crudeNameArray':val,
						'pricingDate':this.yearMonth
					}
	   				xianHuo(para).then(res=>{
	   					if(res.datas[0].dataList.length==0){
	   						this.num =1
	   					}else if(res.datas[0].dataList.length!=0){
	   						this.num =0
	   						for(var i = 0;i<res.datas.length;i++){
		   						option12.series[i].name = res.datas[i].crudeNameE
		   						for(var j=0;j<res.datas[i].dataList.length;j++){
		   							option12.series[i].data.push(res.datas[i].dataList[j].pricing)
		   							if(i==0){
		   								option12.xAxis[0].data.push(res.datas[0].dataList[j].pricingDate)
		   							}
		   							
		   						}
		   					}
		   					myChart.setOption(option12); 
	   					}	
	   				})
				}
			},
			yearMonth:function(newVal){
				var myChart = echarts.init(document.getElementById('main'));
				var option12 = $.extend(true, option12, this.options);
				var para={
					'crudeNameArray':this.arr,
					'pricingDate':newVal
				}
   				xianHuo(para).then(res=>{
   					if(res.datas[0].dataList.length==0){
   						this.num =1
   					}else if(res.datas[0].dataList.length!=0){
   						this.num =0
   						for(var i = 0;i<res.datas.length;i++){
	   						option12.series[i].name = res.datas[i].crudeNameE
	   						for(var j=0;j<res.datas[i].dataList.length;j++){
	   							option12.series[i].data.push(res.datas[i].dataList[j].pricing)
	   							if(i==0){
	   								option12.xAxis[0].data.push(res.datas[0].dataList[j].pricingDate)
	   							}
	   							
	   						}
	   					}
	   					myChart.setOption(option12); 
   					}	
   				})
			}
		},
		created(){
			this.huoZhongFn()
		},
		mounted(){
			
		}
	}
</script>

<style>
	#main{
		
		margin-left: 14%;
	}
	.officalData{
		font-size: 18px;
		height: 300px;
		text-align: center;
		line-height: 300px;
	}
	.tieshui{
		position: absolute !important;
		left: 50px;
    	top: 11px;
	}
</style>


